body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f4f4f4;
  color: #333;
}

h1 {
  font-size: 2.5rem;
  color: #007bff;
  margin-bottom: 20px;
}

h3 {
  font-size: 1.5rem;
  color: #555;
}

button {
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

input[type="text"] {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 300px;
}

input[type="text"]:focus {
  outline: none;
  border-color: #007bff;
}

img {
  max-width: 300px;
  height: auto;
  border: 2px solid #ddd;
  border-radius: 5px;
  margin-top: 20px;
}

div {
  margin-bottom: 20px;
}
